%container {
    width: clamp(300px, 90vw, 1240px);
    margin-inline: auto;
}

%flexer {
    display: flex;
    align-items: center;
    justify-content: space-around;
}

%rounded {
    border-radius: 15px;
}

%btn {
    @extend %rounded;
    color: $light;
    background-color: $primary;
    border: none;
    outline: none;
    cursor: pointer;
}
